.cartShortCut {
  position: relative;
  width: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  background-color: #fff;
}

.countCircle {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -3px;
  left: 20px;
  background-color: #000;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  color: #fff;
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 14px;
}

.cartShortCutList {
  position: absolute;
  top: 80px;
  right: 72px;
  width: 320px;
  max-height: 575px;
  z-index: 3;
  background-color: #ffffff;
  padding: 20px 16px;
}

.cartTitle {
  display: block;
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  max-height: 80%;
  font-size: 16px;
  line-height: 25.6px;
  margin-bottom: 20px;
}

.cartTitleCount {
  font-weight: 500;
}

.cartBtns {
  display: flex;
  justify-content: space-between;
}

.viewBagBtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  width: 140px;
  height: 43px;
  font-family: "Raleway";
  background: #FFFFFF;
  border: 1px solid #1D1F22;
  text-transform: uppercase;
}

.viewBagBtn:hover {
  background-color: #1D1F22;
  color: #ffffff;
  cursor: pointer;
}

.checkOutBtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-family: "Raleway";
  font-weight: 600;
  width: 140px;
  height: 43px;
  border: none;
  color: #FFFFFF;
  background-color: #5ECE7B;
  text-transform: uppercase;
}

.checkOutBtn:hover {
  background-color: #1eb446;
  cursor: pointer;
}