.nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px;
  height: 100%;
}

.nav label {
  text-transform: uppercase;
}

.navButton {
  display: flex;
  align-items: center;
  padding: 0px 16px;
  height: 78px;
  border-bottom: 2px solid #fff;
  font-weight: 400;
  font-size: 16px;
}

.navButton:hover {
  color: #5ece7b;
  cursor: pointer;
}

.activeNavButton {
  display: flex;
  align-items: center;
  height: 78px;
  padding: 0px 16px;
  color: #5ece7b;
  border-bottom: 2px solid #5ece7b;
  font-weight: 600;
  font-size: 16px;
}

.navButtonName:hover {
  cursor: inherit;
}